import emailTransactions from './email-transactions';
// Sends an email to the authenticated user with the group's transactions (sales).
const emailGroupTransactionSales = (groupId, startDate, endDate) => emailTransactions('Group', groupId, 'Sale', startDate, endDate);
// Sends an email to the authenticated user with their personally transactions (sales).
const emailUserTransactionSales = (userId, startDate, endDate) => emailTransactions('User', userId, 'Sale', startDate, endDate);
globalThis.transactionsService = { emailGroupTransactionSales, emailUserTransactionSales };
export { emailGroupTransactionSales, emailUserTransactionSales };
