import xsrfFetch from '../../utils/xsrfFetch';
import getLimitedInventory from './limitedInventory';
import getAssetOwners from './get-asset-owners';
// Removes an asset from the authenticated user's inventory.
const deleteAsset = async (assetId) => {
    const response = await xsrfFetch(new URL(`https://assetgame.roblox.com/asset/delete-from-inventory`), {
        method: 'POST',
        body: JSON.stringify({
            assetId: assetId,
        }),
    });
    if (!response.ok) {
        throw new Error(`Failed to remove asset (${assetId})`);
    }
};
globalThis.inventoryService = { deleteAsset, getLimitedInventory, getAssetOwners };
export { deleteAsset, getLimitedInventory, getAssetOwners };
