import { Batch } from '@tix-factory/batch';
import { addListener, sendMessage } from '@tix-factory/extension-messaging';
import ExpirableDictionary from '../../utils/expireableDictionary';
import xsrfFetch from '../../utils/xsrfFetch';
const messageDestination = 'usersService.getUserByName';
class UserNamesBatchProcessor extends Batch {
    constructor() {
        super({
            levelOfParallelism: 1,
            maxSize: 100,
            minimumDelay: 1000,
            enqueueDeferDelay: 10,
        });
    }
    async process(items) {
        const response = await xsrfFetch(new URL(`https://users.roblox.com/v1/usernames/users`), {
            method: 'POST',
            body: JSON.stringify({
                usernames: items.map((i) => i.key),
                excludeBannedUsers: false,
            }),
        });
        if (!response.ok) {
            throw new Error('Failed to users by names');
        }
        const result = await response.json();
        items.forEach((item) => {
            const user = result.data.find((a) => a.requestedUsername === item.key);
            if (user) {
                item.resolve({
                    id: user.id,
                    name: user.name,
                    displayName: user.displayName,
                });
            }
            else {
                item.resolve(null);
            }
        });
    }
    getKey(item) {
        return item;
    }
}
const batchProcessor = new UserNamesBatchProcessor();
const cache = new ExpirableDictionary(messageDestination, 2 * 60 * 1000);
// Fetches the date when a badge was awarded to the specified user.
const getUserByName = async (name) => {
    return sendMessage(messageDestination, {
        name: name.toLowerCase(),
    });
};
// Listen for messages sent to the service worker.
addListener(messageDestination, (message) => {
    // Check the cache
    return cache.getOrAdd(batchProcessor.getKey(message.name), () => {
        // Queue up the fetch request, when not in the cache
        return batchProcessor.enqueue(message.name);
    });
});
export default getUserByName;
