export * as assets from '../services/assets';
export * as avatar from '../services/avatar';
export * as badges from '../services/badges';
export * as currency from '../services/currency';
export * as followings from '../services/followings';
export * as friends from '../services/friends';
export * as gameLaunch from '../services/game-launch';
export * as gamePasses from '../services/game-passes';
export * as groups from '../services/groups';
export * as inventory from '../services/inventory';
export * as localization from '../services/localization';
export * as premium from '../services/premium';
export * as premiumPayouts from '../services/premium-payouts';
export * as presence from '../services/presence';
export * as privateMessages from '../services/private-messages';
export * as settings from '../services/settings';
export * as thumbnails from '../services/thumbnails';
export * as trades from '../services/trades';
export * as transactions from '../services/transactions';
export * as users from '../services/users';
import { addListener } from '@tix-factory/extension-messaging';
import { manifest } from '@tix-factory/extension-utils';
export * from './notifiers';
chrome.browserAction.setTitle({
    title: `${manifest.name} ${manifest.version}`,
});
chrome.browserAction.onClicked.addListener(() => {
    chrome.tabs.create({
        url: manifest.homepage_url,
        active: true,
    });
});
addListener('extension.reload', async () => {
    setTimeout(() => {
        chrome.runtime.reload();
    }, 250);
}, {
    levelOfParallelism: 1,
    allowExternalConnections: true,
});
